                            </b-col>
                        </b-row>
                    </b-col>
                    </b-col>
                </b-row>
            </b-container>
  </div>
</template>


<script>
/* eslint-disable */
import axios from 'axios'
  export default {
    data() {
      return {
        slide: 0,
        sliding: null,
        productfields:{},
        products: {},
      }
    },
    created () {
    axios.get(`http://localhost:4000/products`)
    .then(response => {
      this.productfields = response.data
    })
    .catch(e => {
      this.errors.push(e)
    })
  },
    methods: {
      onSlideStart() {
        this.sliding = true
      },
      onSlideEnd() {
        this.sliding = false
      },
      onSubmit (evt) {
      evt.preventDefault()
      axios.post(`http://localhost:4000/products`, this.products)
      .then(function (response) {
        console.log(response);
        })
      
    }
    }
  }
  


</script>

